import React,{Component} from 'react';
import ReactDOM from 'react-dom';
import '../Styles/modal.css';


class Modal extends Component{
    constructor(props)
    {
        super(props);
        this.state={

        };
    }

    //the conditional rendering is similar to that of the Items component
    render()
    {
        if (this.props.open === true)
        {
            if (this.props.savedState == true)
            {
                if (this.props.pg == 6)
                {
                    return ReactDOM.createPortal(
                        <React.Fragment >
                            <div className="modalPage">
                                <div className="modal">
                                    <img src={this.props.item.image} alt="image" className="mImage"/>
                                    <div className="mInfo">
                                        <pre>Item  : {this.props.item.Name}</pre>
                                        <pre>Price : ${this.props.item.price}</pre>
                                        <pre>Rating: {Math.round(Math.random() *5) + 1} Star(s)</pre>
                                        <pre>Stock : {this.props.item.stock}</pre>
                                        <pre>#Id   : {this.props.item.id}</pre>
                                    </div>
                                    <button className="mB"onClick={() => this.props.remove(this.props.value)}>REMOVE</button>
                                    <button className="b" onClick={() => this.props.close()}>Close </button>
                                </div>
                            </div>
                        </React.Fragment>
                        ,
                        document.getElementById('portal'));
                }
                else{
                    return ReactDOM.createPortal(
                        <React.Fragment >
                            <div className="modalPage">
                                <div className="modal">
                                    <img src={this.props.item.image} alt="image" className="mImage"/>
                                    <div className="mInfo">
                                        <pre>Item  : {this.props.item.Name}</pre>
                                        <pre>Price : ${this.props.item.price}</pre>
                                        <pre>Rating: {Math.round(Math.random() *5) + 1} Star(s)</pre>
                                        <pre>Stock : {this.props.item.stock}</pre>
                                        <pre>#Id   : {this.props.item.id}</pre>
                                    </div>
                                    <p className="mP">SAVED</p>
                                    <button className="sB" onClick={() => this.props.close()}>Close</button>
                                </div>
                            </div>
                        </React.Fragment>
                        ,
                        document.getElementById('portal'));
                }
            }
            else{
                return ReactDOM.createPortal(
                    <React.Fragment >
                        <div className="modalPage">
                            <div className="modal">
                                <img src={this.props.item.image} alt="image" className="mImage"/>
                                <div className="mInfo">
                                        <pre>Item  : {this.props.item.Name}</pre>
                                        <pre>Price : ${this.props.item.price}</pre>
                                        <pre>Rating: {Math.round(Math.random() *5) + 1} Star(s)</pre>
                                        <pre>Stock : {this.props.item.stock}</pre>
                                        <pre>#Id   : {this.props.item.id}</pre>
                                    </div>
                                <button className="mB"onClick={() => this.props.save(this.props.value)}>ADD TO CART</button>
                                <button className="b" onClick={() => this.props.close()}>Close</button>
                            </div>
                        </div>
                    </React.Fragment>
                    ,
                    document.getElementById('portal'));
            }
        }
        else{
            return null;
        }
    }
}

export default Modal;