import React, { Component } from 'react';

class Menu extends Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            buttonValues : [...this.props.buttonValues], //names of the button
            buttonCounter : [0,1,2,3,4,5,6,7]   //one number for each name
        };
    }

    render()
    {
        //when a menu button is pressed, it returns the value it had, Home has value 0 so it would return that
        //for the sixth subscript, the virtual cart, display the number of items in the cart, if the number is 
        //more than 0
        return (
            <React.Fragment>
                {this.state.buttonCounter.map(value => {
                    if (this.props.itemsInCart > 0 && value === 6)
                    {
                        return <button className="menuBtn" onClick={() => this.props.click(value)}>{this.state.buttonValues[this.state.buttonCounter.indexOf(value)]} ({this.props.itemsInCart})</button>
                    }
                    else{
                        return <button className="menuBtn" onClick={() => this.props.click(value)}>{this.state.buttonValues[this.state.buttonCounter.indexOf(value)]}</button>
                    }
                }
               )
               }
            </React.Fragment>
        );
    }
}

export default Menu;

