import React, { Component } from 'react';
import Menu from './Menu';
import Page from './Page';
import Inventory from '../Inventory.json';
import '../Styles/Website.css';
import '../Styles/Menu.css';
import '../Styles/Departments.css';
import '../Styles/SearchBar.css';
import logo from '../Images/logo.png';

class Website extends Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            //names of button
            buttonValues : ["Home","Food","Appliances","Clothing","Technology","Furniture","Virtual Cart","About us"],
            //keeps track of which page to be on
            currentButton : 0,
            //copy of JSON data
            data : this.setData(),
            //keeps track of which items are saved
            dataClicked : new Array(Inventory.length).fill(false),
            //keeps track of total cost
            total: parseFloat((0).toFixed(2)),
            //keeps track of items in the cart
            itemsInCart : 0,
            //keeps track of the time
            currentTime : this.setTime(),
            //unformatted millitary time
            mTime : this.setMTime(),
            //keeps track of when to show the order information in the virtual cart
            checkedOut:false,
            //the name retrieved from the form
            name : "3.14159265358979",
            //the address retrieved from the form,address is a reserved keyword, so couldnt use it
            addi : "3.14159265358979",
            //a copy of the total to display with the order information
            tempTotal : 0,
            //keeps track of the state of the form
            formNum : 0
        };
    }

    //renderMenu
    renderMenu()
    {
        return (<Menu buttonValues={this.state.buttonValues} click={this.handleMenu} itemsInCart={this.state.itemsInCart}/>);
    } 

    //change the page
    handleMenu = (i) =>
    {
        this.setState({currentButton : i, currentTime : this.setTime(), mTime : this.setMTime()});
    }

    //check whether the time is AM or PM
    handleTime = () =>
    {
        let mTimeArray = this.state.mTime.split(':');
        if (parseInt(mTimeArray[0]) >= 7 && parseInt(mTimeArray[0]) < 19)
        {
            return "Open";
        }
        else
        {
            return "Closed";
        }
    }

    //set the millitary time
    setMTime()
    {
        let today = new Date();
        let hours = today.getHours();
        let minutes = today.getMinutes();
        let time = hours + ":" + minutes;
        return time;
    }

    //change the millitary time into normal time
    setTime()
    {
        let today = new Date();
        let hours = today.getHours();
        let minutes = today.getMinutes();
        let time = hours + ":" + minutes;
        let timeArray = time.split(':');
        let newTime;

        if (timeArray[0] > 0 && timeArray[0] < 13)
        {
            newTime = timeArray[0] + ":";
        }
        else if (timeArray[0] > 12)
        {
            newTime = (timeArray[0] - 12) + ":";
        }
        else
        {
            newTime = 12 + ":"
        }
      
        if (timeArray[1] < 10)
        {
            newTime += "0" + timeArray[1];
        }
        else
        {
            newTime += timeArray[1];
        }

        if (timeArray[0] >= 12)
        {
            newTime += " PM";
        }
        else
        {
            newTime += " AM"
        }

        return (newTime);
    }

    //assign JSON data to array data
    setData()
    {
        let newData = [...Inventory];

        for (let i = 0; i < Inventory.length;i++)
        {
            newData[i].price = Math.round(parseInt(newData[i].price));
        }
        return newData;
    }

    //render the component Page
    renderPage() 
    {
        return <Page 
        value={this.state.currentButton} 
        clickSave={this.saveItem} 
        clickDelete={this.deleteItem}
        inventoryData={this.state.data}
        inventoryBoolean={this.state.dataClicked}
        total={this.state.total}
        checkOut ={this.checkOut}
        checkedOut={this.state.checkedOut}
        resetCheckedOut={this.resetCheckedOut}
        tempTotal={this.state.tempTotal}
        name={this.state.name}
        addi={this.state.addi}
        setName={this.setName}
        setAddi={this.setAddi}
        formNum={this.state.formNum}
        finishForm={this.finishForm}
        />;
    }

    //sets the name var
    setName = (input) =>
    {
        this.setState({name:input});
        let newName = this.state.name;
        if (newName == " ")
        {
            this.setState({name:"3.14159265358979"});
        }
    }

    //sets the address var
    setAddi = (input) =>
    {
        this.setState({addi : input});
        let newAddi = this.state.addi;
        if (newAddi == " ")
        {
            this.setState({addi: "3.14159265358979"})
        }
    }

    //method that changes the state of the form and finishes the form up
    finishForm = () =>
    {
        if (this.state.addi==="3.14159265358979" && this.state.name==="3.14159265358979")
        {
            alert("Please fill out both Name and Address");
        }
        else if (this.state.addi==="3.14159265358979" && this.state.name !=="3.14159265358979")
        {
            alert("Please fill out the Address");
        }
        else if (this.state.addi !=="3.14159265358979" && this.state.name ==="3.14159265358979")
        {
            alert("Please fill out the Name");
        }
        else{
            this.setState({formNum:2});
        }
    }

    //method to change the values of inventoryClicked, save items, turn the value to true
    saveItem = (i) =>
    {
        let newClicked = [...this.state.dataClicked];
        let newTotal = this.state.total;
        let newData = [...this.state.data];
        let newItemsInCart = this.state.itemsInCart;

        if (this.state.data[i].stock == 0)
        {
            console.log(this.state.data[i].Name + " is out of stock.");
            alert(this.state.data[i].Name + " is out of stock.");
        }
        else{
            if (newClicked[i] == false)
            {
                let newStock =parseInt(newData[i].stock);
                newStock--;
                newData[i].stock = newStock.toString();

                newItemsInCart++;
                newTotal += parseInt(this.state.data[i].price);
            }
            newClicked[i] = true;
            this.setState({dataClicked : newClicked, total : parseFloat(newTotal.toFixed(2)), itemsInCart : newItemsInCart, data : newData,currentTime : this.setTime(), mTime : this.setMTime()});
        }
    }

    //method to change the values of inventoryClicked, un-save items, turn the value to false
    deleteItem = (i) =>
    {
        let newData = [...this.state.data];
        let newTotal = this.state.total;
        let newClicked = [...this.state.dataClicked];
        let newItemsInCart = this.state.itemsInCart;
        let newStock =parseInt(newData[i].stock);

        newStock++;
        newData[i].stock = newStock.toString();
        newTotal -= parseInt(this.state.data[i].price);
        newItemsInCart --;
        newClicked[i] = false;
        this.setState({dataClicked : newClicked, total : parseFloat(newTotal.toFixed(2)), itemsInCart : newItemsInCart, data : newData, currentTime : this.setTime(), mTime:this.setMTime()});
    }

    //checkout
    checkOut = () =>
    {
        let newData = this.state.data;
        let newDataClicked = this.state.dataClicked;
        let newTotal = this.state.total;
        let newTempTotal = newTotal;
        let newItemsInCart = this.state.itemsInCart;

        if (this.state.formNum != 2 && this.state.total != 0)
        {
            alert("Please fill out the Order Information Form");
        }
        else
        {
            for (let i = 0;i < newData.length;i++)
            {
                if (newDataClicked[i] == true)
                {
                    newDataClicked[i] = false;
                    newItemsInCart--;
                    newTotal -= parseInt(newData[i].price);
                }
            }

            this.setState({dataClicked : newDataClicked, total : newTotal, itemsInCart : newItemsInCart, currentTime : this.setTime(), mTime : this.setMTime(),checkedOut:true,tempTotal:newTempTotal});
        }
      
    }

    //render the order information
    resetCheckedOut = () =>
    {
        this.setState({checkedOut:false,formNum:0,addi:"3.14159265358979",name: "3.14159265358979"});
    }

    //render the time
    renderTime()
    {
        if (this.handleTime() == "Open")
        {
            return <p className="time">
                    <p className="currentTime">{this.state.currentTime}</p>
                        <p className="storeStatus"> 
                            <pre className="cTime">Store is: </pre>
                            <p style={{color:"green"}} className="cTime" id="timeColor">{this.handleTime()}</p>
                        </p>
                    </p>;
        }
        else{
            return <p className="time">
                    <p className="currentTime">{this.state.currentTime}</p>
                    <p className="storeStatus"> 
                            <pre className="cTime">Store is: </pre>
                            <p style={{color:"red"}} className="cTime" id="timeColor">{this.handleTime()}</p>
                    </p>
                    </p>;
        }
    }

    //render the actual contents of the Page
    render()
    {
        return(
            <div className="Website">
                <div className="nav">
                    <div className="navLogo">
                        <img src={logo} className="navPic" alt="Logo"/>
                        {this.renderTime()}
                    </div> 
                    <div className="navBar">
                        {this.renderMenu()}
                    </div>
                </div>
                <div className="Page">
                    {this.renderPage()}
                </div>
            </div>
        );
    }
}

export default Website;


