import React, { Component } from 'react';
import Items from './Items';
import '../Styles/VirtualCart.css'

class VirtualCart extends Component 
{
    constructor(props)
    {
        super(props);
        this.state = {
        };
    }
 
    //javascript doesn't have a built in method that formats numbers into comma format, maybe it does,
    //but in any case, i had to make one to properly format the total
    comma(num)
    {
        let stringNum = num.toString();
        let length = stringNum.length;
        let newSum = "";
        let final = "";
        let j = 0;
        for (let i = (length - 1); i>= 0 ;i--)
        {
            j++;
            if ((stringNum.length > 3) && ( (j % 3) == 0) && (j != length))
            {
                newSum += stringNum[i];
                newSum += ",";
            }
            else{
                newSum += stringNum[i];
            }
        }
        for (let i = ((newSum.length) - 1); i >= 0 ;i--)
        {
            final += newSum[i];
        }

        return final;
    }

    //render checkout, which contains the total and a list of all the products that were bought in a linear, vertical fashion
    //and contains a checkout button to 'check-out';
    renderCheckout()
    {
        if (this.props.total == 0)
        {
            return (<div className="checkout">
            <h1>
                <p>Total:         $0</p>
            </h1>
        </div>);
        }
        else
        {
            return (<div className="checkout">
            <h1 className="total">
                <p className="elementTotal" id="eTotalT">Total:</p>
                <p className="elementTotal" id="eTotal">${this.comma(this.props.total)}</p>
            </h1>
            <div className="checkedItems">
            {this.props.items.map( c => {
                if (this.props.itemsClicked[this.props.items.indexOf(c)] == true)
                {
                    return <React.Fragment>
                    <p className="elementTotal" id="eTotalT">{c.Name}</p>
                    <p className="elementTotal" id="eTotalM">${c.price}</p>
                    <hr/>
                </React.Fragment>;
                }
            })}
            <pre id="preT">Shipping and Handling                $0</pre>
            <pre id="preT">Taxes and Fees                       $0</pre>
            </div>
            <button onClick={() =>this.props.checkOut()}  className="exit"><i>Check-out</i></button>
        </div>);
        }
    }


    //render the 'form' that asks for the order information like the name and shipping address
    renderForm = () =>{
        if (this.props.total !== 0)
        {
            switch(this.props.formNum)
            {
                case 0: return (
                    <div className="form">
                        <label htmlFor="Name" className="formElement" id="label">Enter Name: </label>
                        <input type="text" name="Name" className="formElement" id="input" placeholder="FirstName LastName" onChange={(e) => {this.props.setName(e.target.value)}}/><br/>
                        <label htmlFor="addi" className="formElement" id="label">Enter Address: </label>
                        <input type="text" name="addi" className="formElement" id="input1"  placeholder="1234 apple dr. Bear DE 19802" onChange={(e) => {this.props.setAddi(e.target.value)}}/><br/>
                        <button className="formElement" id="submit" onClick={() => {this.props.finishForm()}}><i>Submit</i></button>
                    </div>
                );
                break;
                
                case 2 : return (
                    <div className="form">
                        <p className="formElement" id="thankYou">Thank you for your information!</p>
                    </div>
                );

            }
        }
    }

    //this will just print out the information received from the form in a formatted manner with the total cost
    renderCheckoutInformation = () =>
    {   
        if ((this.props.checkedOut == true) && (this.props.formNum == 2))
            return (
            <div className="cInfo">
                <p id="orderInfoTitle">Order Information</p><hr/>
                <p>Name: {this.props.name}</p><hr/>
                <p>Shipping Address: {this.props.addi}</p><hr/>
                <p>Order Total: ${this.comma(this.props.tempTotal)}</p><hr/>
                <button onClick={() => this.props.resetCheckedOut()} id="resetC"><i>Continue Shopping</i></button>
            </div>
            );
    }

    //render the page itself, which contains all of the previously mentioned parts, and a div that actually shows the items that are saved
    render()
    {
        if (this.props.pValue === this.props.dValue)
        {
            return(
                <div>
                    <div className="savedItems">
                        {
                        this.props.items.map(c => {
                            if (this.props.itemsClicked[this.props.items.indexOf(c)] == true)
                            {
                                return (<Items item={c} clicked={this.props.itemsClicked} value={parseInt(c.id)} onClick={this.props.onClick} pValue={this.props.pValue} dValue={this.props.dValue} total={this.props.total}/>);
                            }
                        })}
                    </div>
                    {this.renderCheckout()}
                    {this.renderForm()}
                    {this.renderCheckoutInformation()}
                </div>
            );
        }
    }
}

export default VirtualCart;

