import React, { Component } from 'react';
import '../Styles/aboutUs.css'
import logo from '../Images/logo.png';

class AboutUs extends Component
{
    constructor(props)
    {
        super(props);
        this.state={

        };
    }

    //Render function renders two divs;Sitebio, which contains the bio of the website,
    //Developers, which contains information about the devs and a image that has the 
    //source of the website logo
    render() {
        if (this.props.value === 7)
        {
            return(
                <div >
                    <img src={logo} alt="logo" id="logoPic"/>
                    <div className="siteBio">
                        <p>Our store was founded in 2015 and was named after John McKinly; the first governor of the first state. Just like he was the first of many, we strive to be the first out of every local store by offering a wider array of items than our competitors. 
                            Since its' inception, we have been fortunate enough to open up three locations across the state and the rapid growth of our business led us to the decision of making a website for it. We hope that you have found it easy to use and thank you for your patronage during these trying times.  
                        </p>
                    </div>
                    <div className="developers">
                        <img src="https://www.dtcc.edu/sites/default/files/dtcc-desktop-logo.png" alt="dtcc" id="contactPic"/>
                        <h1 id="contactHeader"><b>Developed by Group 2, CSC 214-401, DTCC</b></h1>
                        <pre className="contactInfo">Contact (Phone) : (302)-345-6373</pre>
                        <pre className="contactInfo">Contact (E-mail): aparbha1@dtcc.edu</pre>
                    </div>
                </div>
            );
        }
    }
}

export default AboutUs;

