import React, { Component } from 'react';
import Items from './Items';
import home from '../Images/Home.jpg'
import '../Styles/Home.css';


class Home extends Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            //three vars for determining three random items of the day. Each var will be used as the subscript for the inventory array
            //which holds the JSON file data in an array format
            rand1 : (Math.floor(Math.random()*(this.props.inventoryData.length))),
            rand2 : (Math.floor(Math.random()*(this.props.inventoryData.length))),
            rand3 : (Math.floor(Math.random()*(this.props.inventoryData.length)))
        };
    }

    //the home page has three major parts:the sidebar with the store information,the picture div, and the div that holds the three random items
    render()
    {
        if (this.props.value === 0)
        {
            return(
                <div className="homePage">
                    <div className="storeHours">
                        <h1 className="homeTitle"><i>Store Hours</i></h1>
                        <pre className="timeInfo">MON : 7AM - 7PM</pre>
                        <pre className="timeInfo">TUE : 7AM - 7PM</pre>
                        <pre className="timeInfo">WED : 7AM - 7PM</pre>
                        <pre className="timeInfo">THU : 7AM - 7PM</pre>
                        <pre className="timeInfo">FRI : 7AM - 7PM</pre>
                        <pre className="timeInfo">SAT : 7AM - 7PM</pre>
                        <pre className="timeInfo">SUN : 7AM - 7PM</pre>
                        <h1 className="homeTitle"><i>Locations</i></h1>
                        <p className="homeInfo">1345 Crabapple Dr.
                             Bear DE,19703
                        </p>
                        <p className="homeInfo">5260 Dawn rd.
                             Dover DE,19901
                        </p>
                        <p className="homeInfo">1502 N. Dupont rd.
                             Wilmington DE,19807
                        </p>
                        <h1 className="homeTitle"><i>Contact Us</i></h1>
                        <p className="homeInfo">Bear : (302)-345-6373</p>
                        <p className="homeInfo">Dover : (302)-567-3243</p>
                        <p className="homeInfo">Wilmington : (302)-625-3526</p>
                    </div>
                    <div className="todaysDeals">
                        <img src={home} alt="home" className="homePic"/>
                        <hr className="hr"/>
                        <div className="homeItems">
                            <Items 
                            pValue={this.props.value} 
                            dValue={0} 
                            value={this.props.inventoryData[this.state.rand1].id}
                            item={this.props.inventoryData[this.state.rand1]}
                            onClick={this.props.clickSave}
                            clicked={this.props.inventoryBoolean}
                            />
                            <Items 
                            pValue={this.props.value} 
                            dValue={0} 
                            value={this.props.inventoryData[this.state.rand2].id}
                            item={this.props.inventoryData[this.state.rand2]}
                            onClick={this.props.clickSave}
                            clicked={this.props.inventoryBoolean}
                            />
                            <Items 
                            pValue={this.props.value} 
                            dValue={0} 
                            value={this.props.inventoryData[this.state.rand3].id}
                            item={this.props.inventoryData[this.state.rand3]}
                            onClick={this.props.clickSave}
                            clicked={this.props.inventoryBoolean}
                            />
                        </div>
                    </div>
               </div>
            );
        }
    }
}

export default Home;