import React, { Component } from 'react';

class SearchBar extends Component
{
    constructor(props)
    {
        super(props);
        this.state = {

        };
    }

    render()
    {
        //very basic searchbar, as each new character is added, pass each one back to the calling component 
        //so that it can be used in conjunction with the filter
        return (
                 <input type="search"
                    className="search"
                    placeholder={"Search"+this.props.department}
                    onChange={(e) => this.props.handleChange(e)}
                    />
                );
    }
}

export default SearchBar;

