import React, { Component } from 'react';
import AboutUs from './AboutUs';
import CurrentPage from './CurrentPage';
import Home from './Home';

class Page extends Component
{
    constructor(props)
    {
        super(props);
        this.props = {
        };
    }

    renderCurrentPage()
    {
        //based on the button pressed, either return the Home, About Us or CurrentPage
        switch(this.props.value)
        {
            case 0:
                return <Home value={this.props.value} clickSave={this.props.clickSave}  inventoryData={this.props.inventoryData}
                inventoryBoolean={this.props.inventoryBoolean} />;
            break;
            case 7:

                return <AboutUs value={this.props.value}/>;
            break;
            default : return <CurrentPage value={this.props.value} 
            clickSave={this.props.clickSave} 
            clickDelete={this.props.clickDelete}
            inventoryData={this.props.inventoryData}
            inventoryBoolean={this.props.inventoryBoolean}
            total={this.props.total}
            checkOut={this.props.checkOut}
            checkedOut={this.props.checkedOut}
            resetCheckedOut={this.props.resetCheckedOut}
            tempTotal={this.props.tempTotal}
            name={this.props.name}
            addi={this.props.addi}
            setName={this.props.setName}
            setAddi={this.props.setAddi}
            formNum={this.props.formNum}
            nameNum={this.props.nameNum}
            addiNum={this.props.addiNum}
            incrementAddiNum={this.props.incrementAddiNum}
            incrementNameNum={this.props.incrementNameNum}
            finishForm={this.props.finishForm}
            />;
        }
    }

    //render whatever was decided
    render()
    {
        return(
            <div>
                {this.renderCurrentPage()}
            </div>
        );
    }
}

export default Page;

