import React, { Component } from 'react';
import Modal from './Modal';
import '../Styles/Item.css';

class Items extends Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            modalOpen:false //determines the state of the modal, by default the modal is off
        };
    }

    //turn the modal on
    modalOn = () =>
    {
        let newModal = true;
        this.setState({modalOpen : newModal});
    }

    //turn the modal off
    modalOff = () =>
    {
        let newModal = false;
        this.setState({modalOpen : newModal});
    }


    //render the items component
    //and based on the two if statements, render three different possible types
    //firstly, if the item is saved, then it will look different from an unsaved item and will display 'SAVED'
    //instead of displaying the button to add to cart
    //going further, if the item is going to be rendered into the virtual cart page, then display a button that displays remove 
    render()
    {
        if (this.props.pValue === this.props.dValue)
        {
            if (this.props.clicked[this.props.value] === true)
            {
                if (this.props.dValue == 6)
                {
                    return(
                        <React.Fragment >
                            <button className="item" >
                                <img className="iPic" id="remove" src={this.props.item.image} alt={this.props.item.Name}/>
                                <p><b>{this.props.item.Name}</b></p>
                                {/* <p>Stock: {this.props.item.stock}</p> */}
                                <p>Price: ${this.props.item.price}</p>
                                <button onClick={this.modalOn} className="viewItem"><p><i>VIEW</i></p></button><br/><br/>
                                <button onClick={() => this.props.onClick(this.props.value)} className="removeItem"><p><i>REMOVE</i></p></button>
                                <Modal open={this.state.modalOpen} close={this.modalOff} item={this.props.item} savedState={true} pg={6} remove={this.props.onClick} value={this.props.value}/>
                            </button>
                        </React.Fragment>
                    );
                }
                else{
                    return(
                        <React.Fragment>
                            <button  className="item">
                                <img className="iPic"src={this.props.item.image} alt={this.props.item.Name}/>
                                <p><b>{this.props.item.Name}</b></p>
                                {/* <p>Stock: {this.props.item.stock}</p> */}
                                <p>Price: ${this.props.item.price}</p>
                                <button onClick={this.modalOn} className="viewItem"><p><i>VIEW</i></p></button><br/><br/>
                                <p className="savedItem"><i>SAVED</i></p>
                            </button>
                            <Modal open={this.state.modalOpen} close={this.modalOff} item={this.props.item} savedState={true} pg={0}/>
                        </React.Fragment>
                    );
                }
            }
            else{
                return(
                    <React.Fragment>
                        <button className="item" >
                            <img className="iPic"src={this.props.item.image} alt={this.props.item.Name}/>
                            <p><b>{this.props.item.Name}</b></p>
                            {/* <p>Stock: {this.props.item.stock}</p> */}
                            <p>Price: ${this.props.item.price}</p>
                            <button onClick={this.modalOn} className="viewItem"><p><i>VIEW</i></p></button><br/><br/>
                            <button onClick={() => this.props.onClick(this.props.value)} className="saveItem"><p><b>ADD TO CART</b></p></button>
                        </button>
                        <Modal open={this.state.modalOpen} close={this.modalOff} item={this.props.item} savedState={false} pg={0} save={this.props.onClick} value={this.props.value}/>
                    </React.Fragment>
                );
            }
        }
    }
}

export default Items;

